import { Component } from "react";
import axios from "axios";

class BasisData extends Component{
  constructor(props){
      super(props);
      this.state= {
          dataApi: [],
          edit:false,
          dataPost:{
              id:0,
              nama_karyawan:'',
              jabatan:'',
              jenis_kelamin:'',
              tanggal_lahir:''
          }
      };
      this.handleRemove = this.handleRemove.bind(this);
      this.inputChange = this.inputChange.bind(this);
      this.onSubmitForm = this.onSubmitForm.bind(this);
  }
  reloadData(){
    axios.get("http://localhost:3004/data-karyawan")
    .then(res =>{
        this.setState({
            dataApi:res.data,
            edit: false
        })
    });
  }
  inputChange(e){
      let newdataPost = {...this.state.dataPost};
      if(this.state.edit === false){
        newdataPost['id']=new Date().getTime();
      }
      newdataPost[e.target.name]=e.target.value;

      this.setState({
          dataPost: newdataPost
      }, () => console.log(this.state.dataPost))
  }
  onSubmitForm=()=>{
      if(this.state.edit === false){
        axios.post(`http://localhost:3004/data-karyawan`, this.state.dataPost).then(()=>{this.reloadData()});
        this.reloadData();
        this.clearData();

      }else{
        axios.put(`http://localhost:3004/data-karyawan/${this.state.dataPost.id}`, this.state.dataPost).then(()=>{
            this.reloadData();
            this.clearData();
        })
      }
  }
  handleRemove(e){
    console.log(e.target.value);
    fetch(`http://localhost:3004/data-karyawan/${e.target.value}`,{
        method:"DELETE"
    }).then(res => this.reloadData());
  }
  getDataId = e =>{
      axios
      .get(`http://localhost:3004/data-karyawan/${e.target.value}`)
      .then(res=>{
          this.setState({
              dataPost:res.data,
              edit:true
          })
      });
  }
  clearData =()=>{
    let newdataPost = {...this.state.dataPost};
    newdataPost['id']="";
    newdataPost['nama_karyawan']="";
    newdataPost['jabatan']="";
    newdataPost['tanggal_lahir']="";
    this.setState({
        dataPost :newdataPost
    })
  }
  componentDidMount(){
     this.reloadData();
  }
  render(){
    return(
      <div>
        <input type="text" name='nama_karyawan' placeholder="Masukkan Nama" value={this.state.dataPost.nama_karyawan} onChange={this.inputChange}/>
        <input type="text" name='jabatan' placeholder="Masukkan Jabatan" value={this.state.dataPost.jabatan} onChange={this.inputChange}/>
        <label> Jenis Kelamin:</label>
            <select name="jenis_kelamin" onChange={this.inputChange}>
            <option value="Laki-Laki">Laki-Laki</option>
            <option value="Perempuan">Perempuan</option>
            </select>
        <input type="date" name="tanggal_lahir" value={this.state.dataPost.tanggal_lahir} onChange={this.inputChange}/>
        <button type="submit" onClick={this.onSubmitForm} style={{backgroundColor: "blue"}}>Simpan Data</button>
        {this.state.dataApi.map((dat, index)=>{
            return(
                <div key={index}>
                    <p>{dat.nama_karyawan}</p>
                    <p>{dat.jabatan}</p>
                    <p>{dat.jenis_kelamin}</p>
                    <p>{dat.tanggal_lahir}</p>
                    <button value={dat.id} onClick={this.handleRemove} style={{backgroundColor: "orange"}}>Delete</button>
                    <button value={dat.id} onClick={this.getDataId} style={{backgroundColor: "green"}}>Edit</button>
                </div>
            );
        })}
      </div>
    )
  }
}
export default BasisData;